import React from 'react';
import { Menu, Icon, Layout } from 'antd';
import 'antd/dist/antd.css';
import './index.css';
import Profile from './Pages/Profile';

const SubMenu = Menu.SubMenu;
const MenuItemGroup = Menu.ItemGroup;
const { Header, Footer } = Layout;

export default class WebTabs extends React.Component {
  state = {
    current: 'home',
  }

  handleClick = (e) => {
    console.log('click ', e);
    this.setState({
      current: e.key,
    });
  }

  handleTitleClick = ({key, domEvent}) => {
    console.log(key);
    this.setState({
      current: key
    });
  }

  render() {
    return (
      <Layout>
        <Header style={{ position: 'fixed', zIndex: 1, width: '100%', height: 'auto' }}>
          <Menu
            theme='dark'
            onClick={this.handleClick}
            selectedKeys={[this.state.current]}
            mode='horizontal'
          >
            <Menu.Item key='home'>
              <Icon type='user' />
              About Me
            </Menu.Item>

            <SubMenu
              title={
                <span className='submenu-title-wrapper'>
                  <Icon type='thunderbolt' /> Blog
                </span>
              }
              key='blog'
              onTitleClick={this.handleTitleClick}
            >
              <MenuItemGroup title='Item 1' key='food'>
                <Menu.Item key='setting:1'>Option 1</Menu.Item>
                <Menu.Item key='setting:2'>Option 2</Menu.Item>
              </MenuItemGroup>

              <MenuItemGroup title='Item 2'>
                <Menu.Item key='setting:3'>Option 3</Menu.Item>
                <Menu.Item key='setting:4'>Option 4</Menu.Item>
              </MenuItemGroup>
            </SubMenu>

            <Menu.Item key='contact'>
              <Icon type='phone' />
              Contact Me
            </Menu.Item>
          </Menu>
        </Header>
        <Profile />
        <Footer style={{
            position: 'fixed',
            textAlign: 'center',
            bottom: 0,
            left: 0,
            right: 0,
          }}>
          A boba a day keeps reality away.
        </Footer>
      </Layout>
    );
  }

}
